import React from 'react';

import {
  FooterContainer,
  FooterWrapper,
  FooterLogo,
  FooterCopyright
} from './FooterElements';

const FooterSection = () => {
  return (
    <>
      <FooterContainer>
        <FooterWrapper>            
          <FooterLogo to="#">Home Repair</FooterLogo>
          <FooterCopyright>Home Repair © 2021. All rights reserved.</FooterCopyright>
        </FooterWrapper>
      </FooterContainer>
    </>
  )
}

export default FooterSection;
