import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import './App.css';
import Home from './pages/index';
import Electrical from './pages/newIndex';
import Carpentry from './pages/servicePage2';
import Plumbing from './pages/servicePage3';

function App() {

  return (
    <Router > 
      <Switch>
        <Route path="/" exact component={Home}></Route>
        <Route path="/houserepair" exact component={Home}></Route>
        <Route path="/Electrical" exact component={Electrical}></Route>
        <Route path="/Carpentry" exact component={Carpentry}></Route>
        <Route path="/Plumbing" exact component={Plumbing}></Route>
      </Switch>
    </Router>
  );
}

export default App;
