import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import {
  ServicesContainer,
  ServicesH1,
  ServicesWrapper,
  ServicesCard,
  ServicesIcon,
  ServicesH2,
  ServicesP
} from './ServicesElements'

import Icon1 from '../../images/electrical.jpg';
import Icon2 from '../../images/carp.jpg';
import Icon3 from '../../images/plumb.jpg';

const ServicesSection = () => {
  return (
    <>
      <ServicesContainer id="services">
        <ServicesH1>Our Services</ServicesH1>
        <ServicesWrapper>
          <ServicesCard>
          
            <ServicesIcon src={Icon1}></ServicesIcon>
            <ServicesH2>Electrical services </ServicesH2>
            <ServicesP>
            “Safety first - leave it to the experts. All our electricians are appropriately qualified."
<Link to="/Electrical">Know More</Link>
</ServicesP>
          </ServicesCard>
          <ServicesCard>
            <ServicesIcon src={Icon2}></ServicesIcon>
            <ServicesH2>Carpentering Services</ServicesH2>
            <ServicesP>
            “Fitting a door or window properly, requires skill and experience.”
           
<Link to="/Carpentry">Know More</Link>
</ServicesP>
          </ServicesCard>
          <ServicesCard>
            <ServicesIcon src={Icon3}></ServicesIcon>
            <ServicesH2>Plumbing Services</ServicesH2>
            <ServicesP>
            "We can meet all your small plumbing repair needs!”
<Link to="/Plumbing">Know More</Link>
</ServicesP>

          </ServicesCard>
        </ServicesWrapper>
      </ServicesContainer>
    </>
  )
}

export default ServicesSection;
