import React from 'react'
import { ButtonLinkScroll } from '../ButtonElements'
import {
  InfoContainer,
  InfoWrapper,
  InfoRow,
  Column1,
  Column2,
  TextWrapper,
  TopLine,
  Headline,
  Description,
  BtnWrap,
  ImgWrap,
  Img
} from './infoServiceElements';

const ServiceSection = ({data}) => { 

    // const text = data.description;
    // const data.description = text.split('\n').map(str => <p>{str}</p>);
    // console.log(newText);
    

  return (
    <>
     
      <InfoContainer id={data.id} lightBg={data.lightBg}>
        <InfoWrapper>
        <TopLine>{data.topLine}</TopLine>
                <Headline lightText={data.lightText}>{data.headline}</Headline>
                <h3>{data.subheading1}</h3>
 
               
        <p lightText={data.lightText} >{data.description.split('\n').map(str => <p>{str}</p>)}</p>
        <br></br>
          <InfoRow imgStart={data.imgStart}>
            
            <Column1>
              <TextWrapper>
           
              
                {data.subheading1 != null ? <h3>{data.subheading2}</h3>  : null}
                {data.description2 != null ? <Description lightText={data.lightText}>{data.description2.split('\n').map(str => <p>{str}</p>)}</Description> : null}
                {data.subheading1 != null ? <h3>{data.subheading3}</h3>  : null}
                {data.description3 != null ? <Description lightText={data.lightText }>{data.description3.split('\n').map(str => <p >{str}</p>)}</Description> : null}
                
                <BtnWrap>
                  <ButtonLinkScroll to={data.buttonScrollTo} smooth={true} duration={500} spy={true} exact="true" offset={-70} primary={data.buttonPrimary} dark={data.buttonDark}>
                    {data.buttonLabel}
                  </ButtonLinkScroll>
                </BtnWrap>
              </TextWrapper>
            </Column1>
            <Column2>
            <ImgWrap>
                <Img src={data.imgSource} alt={data.imgAlt}></Img>
              </ImgWrap>
            </Column2>
            
          </InfoRow>
          
        </InfoWrapper>
      </InfoContainer>
    </>
  )
}

export default ServiceSection;
