import React from 'react';
import ServiceSection from '../components/info_service_section';
import Navbar from '../components/navbar';
import ParticleBackground from '../components/ParticleBackground';
import {AboutData, DiscoverData, WelcomeData, ElectricalServices , CarpentryServices , PlumbingServices} from '../components/info_section/Data';
import ContactUsSection from '../components/contact_us_section';
import FooterSection from '../components/footer';
import ServicesSection from '../components/services_section';
import { useEffect } from "react";
import { useLocation } from "react-router-dom";

const Carpentry = () => {
  const routePath = useLocation();
  const onTop = () => {
    window.scrollTo(0, 0);
  }
  useEffect(() => {
    onTop()
  }, [routePath]);
  return (
    <>
      {/* <Navbar></Navbar> */}
     
      <ServiceSection data={CarpentryServices}></ServiceSection>
      <ContactUsSection></ContactUsSection>
      <FooterSection></FooterSection>
      <ParticleBackground></ParticleBackground>
    </>
  );
}

export default Carpentry;